<template>
  <div>
    <img src="../../assets/user-banner.png" style="width: 100%"/>
    <div id="user-info">
      <mu-row>
        <mu-col span="6">
          <img id="avatar" src="../../../static/images/naicha3.jpg">
        </mu-col>
        <mu-col span="6" class="username">
          <div id="username">
            {{user.userName}}
          </div>
        </mu-col>
      </mu-row>
    </div>
    <div id="op-list">
      <mu-list>
        <mu-list-item button :ripple="false" @click="tips">
          <mu-list-item-action>
            <mu-icon value="inbox"></mu-icon>
          </mu-list-item-action>
          <mu-list-item-title>地址: {{user.address}}</mu-list-item-title>
        </mu-list-item>
        <mu-list-item button :ripple="false">
          <mu-list-item-action>
            <mu-icon value="inbox"></mu-icon>
          </mu-list-item-action>
          <mu-list-item-title>余额</mu-list-item-title>
        </mu-list-item>
      </mu-list>
    </div>

    <el-dialog title="添加地址" :visible.sync="addressDialogVisible" style="font-size: 12px;" width="80%">
      <div>
        <el-form ref="spreadItem" :inline="true">
          <el-cascader
            v-model="address"
            :options="addressOptions"
            @change="cascaderSelector"></el-cascader>
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入内容"
            v-model="addressDetail">
          </el-input>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer" style="text-align: center">
        <el-button class="buy-btn" @click="addAddress" >确定</el-button>
        <el-button @click="addDialogVisible = false">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  export default {
    name: 'User',
    data () {
      return {
        addressDetail: ''
      }
    },
    computed: {
      ...mapState({
        user: state => state.user.user
      })
    },
    methods: {
      addAddress() {
        this.$axios.post('/user/address', {

        }).then(res => {

        }).catch(err => {
          console.error(err)
        })
      },
      tips() {
        this.$message.warning("功能尚在开发中...")
      }
    }
  }
</script>

<style scoped>
  #username {
    margin: auto;
    position: relative;
    top: 17px;
    font-size: 19px;
    font-weight: 600;
  }
  #avatar {
    width: 64px;
    border-radius: 45px;
    top: 15px;
    position: relative;
  }
  #user-info {
    position: relative;
    top: -46px;
    height: calc(15vh);
    /*border-top-right-radius: 8px;*/
    /*border-top-left-radius: 8px;*/
    border-radius: 8px;
    margin: 0px 8px;
    background-color: white;
  }

  #op-list {
    background-color: white;
    border-radius: 8px;
    margin: 0px 8px;
  }
</style>



